html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-planet-00 {
  width: 1.01em;
  height: 1em;
  fill: #69899e;
}

.icon-planet-01 {
  width: 0.98em;
  height: 1em;
  fill: #e5a248;
}

.icon-planet-02 {
  width: 0.91em;
  height: 1em;
  fill: #b29a76;
}

.icon-planet-03 {
  width: 1.01em;
  height: 1em;
  fill: #9abdcc;
}

@keyframes spinsun {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(-360deg);
  }
}

@keyframes orbitearth {
  0% {
    transform: translate3d(0, 0, 0);
    z-index: 4;
  }
  49% {
    z-index: 4;
  }
  50% {
    z-index: -4;
    transform: translate3d(-210px, -100px, 0);
  }
  99% {
    z-index: -4;
  }
  100% {
    z-index: 4;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes orbitmars {
  0% {
    z-index: 5;
    transform: translate3d(0, 0, 0);
  }
  49% {
    z-index: 5;
  }
  50% {
    z-index: -5;
    transform: translate3d(230px, -90px, 0);
  }
  99% {
    z-index: -5;
  }
  100% {
    z-index: 5;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes orbitjupiter {
  0% {
    z-index: 4;
    transform: translate3d(0, 0, 0);
  }
  49% {
    z-index: 4;
  }
  50% {
    transform: translate3d(220px, 0, 0);
    z-index: -4;
  }
  99% {
    z-index: -4;
  }
  100% {
    transform: translate3d(0, 0, 0);
    z-index: 4;
  }
}

@keyframes pulsar {
  0% {
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 30px 10px #fdb227;
  }
  100% {
    transform: scale(1);
  }
}

/* Keep planet image flat */
@keyframes anti-spin {
  from {
    transform: rotateY(10deg);
  }
  to {
    transform: rotateY(-360deg);
  }
}

@keyframes anti-spin-rings {
  from {
    transform: rotateY(10deg) rotateX(73deg);
  }
  to {
    transform: rotateY(-360deg) rotateX(73deg);
  }
}

/* scene wrapper */
body {
  height: 100%;
}

.out {
  overflow: hidden;
  height: 100%;
}

.preloader {
  margin: 0 auto;
  position: relative;
  perspective: 1000px;
  padding: 220px 0px;
  max-width: 250px;
  width: 100%;
  display: block;
  perspective-origin: 60% 50%;
}

.preloader > div {
  position: relative;
  margin: 0 auto;
  transform-style: preserve-3d;
  height: 0px;
}

.sun {
  width: 60px;
  top: 0px;
  display: block;
  z-index: 1;
  height: 60px !important;
}

.sun .star {
  width: 60px;
  height: 60px;
  background-image: url("../img/sun.svg");
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 250px;
  margin: 0 auto;
  position: relative;
  display: block;
  animation: spinsun 40s infinite linear;
}

.sun .star::before {
  content: "";
  border-radius: 250px;
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 165, 0, 0.61);
  z-index: 1;
  top: 3px;
  box-shadow: 0 0px 50px 10px  #f9bb4b;
  transition: box-shadow .24s linear;
  animation: pulsar 4s infinite linear;
  left: 0;
}

.planet {
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  animation-iteration-count: infinite;
  overflow: hidden;
  display: inline-block;
}

.earth {
  position: absolute;
  z-index: 4;
  animation: orbitearth 4s infinite linear;
  top: 0;
  left: 100%;
}

.earth .planet {
  width: 40px;
  height: 40px;
  background-image: url("../img/earth.svg");
  background-size: cover;
}

.mars {
  position: absolute;
  z-index: 5;
  animation: orbitmars 5s infinite linear;
  top: 0;
}

.mars .planet {
  width: 22px;
  height: 22px;
  background-image: url("../img/mars.svg");
}

.jupiter {
  position: absolute;
  width: 100%;
  max-width: 1100px;
  z-index: 6;
  animation: orbitjupiter 4.5s infinite linear;
  top: -45px;
}

.jupiter .planet {
  width: 30px;
  height: 30px;
  background: url("../img/jupiter.svg") no-repeat;
  background-size: cover;
}

.icon-planet-00 {
  width: 100px;
  height: 100px;
}
